import React, {useContext, useEffect, useState} from 'react';
import {addSubmission, fetchQuestions, getStoredQuestions, syncSubmissions} from "./db";
import Logo from './assets/images/logo.png'
import {useNavigate, useSearchParams} from "react-router-dom";
import {SurveyContext} from "./surveyContext";
import axios from "axios";
import {toast} from "react-toastify";

const useOnline = () => {
    const [isOnline, setIsOnline] = useState(navigator.onLine);

    useEffect(() => {
        const handleOnline = () => setIsOnline(true);
        const handleOffline = () => setIsOnline(false);

        window.addEventListener('online', handleOnline);
        window.addEventListener('offline', handleOffline);

        return () => {
            window.removeEventListener('online', handleOnline);
            window.removeEventListener('offline', handleOffline);
        };
    }, []);

    return isOnline;
};


const SurveyForm = () => {
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const email = searchParams.get('email')
    const [questions, setQuestions] = useState([]);
    const [activeQuestion, setActiveQuestion] = useState(0)
    const [selectedAnswer, setSelectedAnswer] = useState([])
    const [showResult, setShowResult] = useState(false)
    const [selectedAnswerIndex, setSelectedAnswerIndex] = useState(null)
    const { setSurveyCompleted } = useContext(SurveyContext);
    const [result, setResult] = useState({
        score: 0, correctAnswers: 0, wrongAnswers: 0,
    })
    const [selectedOptions, setSelectedOptions] = useState([]);


    const toggleOption = (option) => {
        if (selectedOptions.includes(option)) {
            setSelectedOptions(selectedOptions.filter((item) => item !== option));
        } else {
            setSelectedOptions([...selectedOptions, option]);
        }
    };


    const isOnline = useOnline();

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!isOnline) {
            addSubmission({email, selectedAnswer});
            toast.error('Formulaire soumis hors ligne. Il sera synchronis lorsque le rseau sera disponible.');
            setSurveyCompleted(true)
            navigate({
                pathname: '/video'
            });
            return;
        }

        try {
            const body = {
                email: email,
                selectedAnswer: selectedAnswer
            }
            const response = await axios.post('https://admin-panel.masscom-ci.com/api/submit', body)

            if (response?.data?.status) {
                toast.success('Flicitations! Vous pouvez  prsent accder aux vidos / Playlists');
                setSurveyCompleted(true)
                navigate({
                    pathname: '/video'
                });
            } else {
                toast.error("Accdez aux vidos /Playlists")
                setSurveyCompleted(true)
                navigate({
                    pathname: '/video'
                });
            }
        } catch (error) {
            console.log(error?.response?.data?.message)
            toast.error(error?.response?.data?.message);
        }
    };

    useEffect(() => {
        if (!email) {
            navigate({
                pathname: '/',
            });
        }

        const fetchSurveyQuestions = async () => {
            if (isOnline) {
                await fetchQuestions();
            }

            const storedQuestions = await getStoredQuestions();


            setQuestions(storedQuestions);
        };

        fetchSurveyQuestions();
    }, [email, isOnline, navigate]);


    const questionObject = questions[0]?.questions[activeQuestion]


    const onClickNext = () => {
        setSelectedAnswerIndex(null)
        setResult((prev) => selectedAnswer ? {
            ...prev, score: prev.score + 5, correctAnswers: prev.correctAnswers + 1,
        } : {...prev, wrongAnswers: prev.wrongAnswers + 1})
        if (activeQuestion !== questions[0]?.questions?.length - 1) {
            setActiveQuestion((prev) => prev + 1)
        } else {
            setActiveQuestion(0)
            setShowResult(true)
        }
    }

    const onAnswerSelected = (question, answer, type) => {
        setSelectedAnswer((prevSelectedOptions) => {
            const existingOption = prevSelectedOptions.find((item) => item.question === question);
            if (existingOption) {
                return prevSelectedOptions.map((item) => {
                    if (item.question === question) {
                        console.log("answer", answer)
                        return {
                            ...item, options: [...item.options, answer],
                        };
                    }
                    return item;
                });
            } else {
                return [...prevSelectedOptions, {
                    question, options: [answer],
                },];
            }
        });

        setSelectedAnswerIndex(true)
    }


    const addLeadingZero = (number) => (number > 9 ? number : `0${number}`)

    return (<>
        <div  style={{width: '100vw', height: '100vh' ,backgroundPosition: "center", backgroundImage: `url(${questions[0]?.company_bg})`}}>
            <nav style={{textAlign: "center"}}>
                <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
            </nav>
            <div className="quiz-container">
                {<div className={"text-left"}>
                    <div className={"companyLogo"} style={{textAlign: 'center'}}>
                        <h1 className={"font-bold text-2xl"}>
                            {questions[0]?.survey_title}
                        </h1>
                        <img src={questions[0]?.company_logo} width={"60px"} alt={"Company logo"}/>

                        <div className={"text-left"}>
                            {questionObject?.type === 'MCQ' ? <span>Veuillez slectionner une option</span> : questionObject?.type === 'MAQ' ? <span>Vous pouvez slectionner plusieurs options</span> : <span>Veuillez crire votre rponse dans la zone de saisie</span>}
                        </div>

                    </div>
                    <div>
                        <span className="active-question-no">{addLeadingZero(activeQuestion + 1)}</span>
                        <span className="total-question">/{addLeadingZero(questions[0]?.questions?.length)}</span>
                    </div>

                    <h2 className={"text-left"}>{questionObject?.question}</h2>
                    {questionObject?.type === 'input' ? <div>
                            <input type={"text"} onChange={(e) => onAnswerSelected(questionObject?.id, e.target.value)}/>
                        </div> :
                        questionObject?.type === 'MCQ' ?
                            questionObject?.options.map((answer, index) => (
                                <div className="flex items-center mb-4">
                                    <input onChange={(e) => onAnswerSelected(questionObject?.id, parseInt(e.target.value), 'MCQ')} id="default-radio-1" type="radio" value={answer?.id} name="default-radio"
                                           className="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
                                    <label htmlFor="default-radio-1"
                                           className="ml-2 text-sm font-medium">{answer?.option}</label>
                                </div>
                            ))
                            : <ul>
                                {questionObject?.options.map((answer, index) => (
                                    <li
                                        onClick={() => {
                                            onAnswerSelected(questionObject?.id, answer?.id, index)
                                            toggleOption(answer?.option)
                                        }}
                                        key={index}
                                        className={selectedOptions.includes(answer?.option) ? 'selected-answer' : ''}
                                    >
                                        {answer?.option}
                                    </li>))}

                            </ul>}
                    <div className="flex-right">
                        <button
                            onClick={activeQuestion === questions[0]?.questions?.length - 1 ? handleSubmit : onClickNext}
                            disabled={selectedAnswerIndex === null}>
                            {activeQuestion === questions[0]?.questions?.length - 1 ? 'Finish' : 'Next'}
                        </button>
                    </div>
                </div>}
            </div>
        </div>

    </>)
};

export default SurveyForm;
